import React, { Fragment, useEffect, useState } from 'react';
import Login from './containers/Login';
import Pay from './containers/Pay';
import { Box } from '@material-ui/core';
import { Route, Switch, withRouter, Redirect } from 'react-router-dom';
import { Connect, mapDispatchToProps, mapStateToProps } from './redux';
import { Api, updateJWTToken } from './shared/helper';
import Loader from './components/loader';
import useStyles from './styles';
let Callback = window.Callback
function App(props) {
  const { setByMultipleFields } = props,
  [state, setState] = useState({
    loading: false,
    error: null
  }),
  { loading, error } = state,

  classes = useStyles(),
  decrypt=()=>{
    setState({ ...state, loading: true })
    Api('/decrypt_app_payment',{encrypted:true, data: props?.location?.search.split('=')[1]}).then((resp)=>{
      if(resp?.error)
        setState({ ...state, loading: false, error: resp?.error })
      else{
        setByMultipleFields({ checkOutInfo: resp.transaction, app:  resp.app, recepient: resp.recepient})
        updateJWTToken(resp?.access_token)
        setState({ ...state, loading: false })
      }
    }).catch(err => {
        setState({ ...state, loading: false, error: err ? "Provide payment information." : "" })
    })
  }

  let routes = (
    <Switch>
      <Route path="/" exact component={Login} />
      <Redirect to="/" />
    </Switch>
  );
  if (props.loggedIn) {
    routes = (
      <Switch>
        <Route path="/pay" exact component={Pay} />
        <Redirect to="/pay" />
      </Switch>
    );
  }



  useEffect(() => {
    decrypt()
    return () => {

    }
  }, [])
  return (
    <Fragment>
      {loading ? <div className={classes.load}><Loader/></div> : error ? <div className={classes.main}>
        <Box className={classes.payBg} boxShadow={3}>
        <div className={classes.paymentHeader}><div>Pay with Benkiko</div> <img className={classes.payImg} src="https://benkiko.s3.us-east-2.amazonaws.com/logo.png" /> </div>
        <div className={classes.appError}>{error}</div>
        </Box>
        </div>  : routes}
    </Fragment>
  );
}

export default withRouter(Connect(mapStateToProps, mapDispatchToProps)(App));
